<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-4">
      <button class="btn btn-primary"
        @click="editCouponModal(true)"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="100"></th>
          <th width="130"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in coupons" :key="item.id">
          <td>{{ item.title }}</td>
          <td>{{ item.code }}</td>
          <td>{{ item.percent }}% </td>
          <td>{{ item.due_date }}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm"
              @click="editCouponModal(false, item)"></button>
            <button class="btn btn-outline-danger btn-sm"
              @click="deleteCouponModal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- Edit Coupon Modal -->
    <div class="modal fade" id="editCouponModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-12">
                <div class="form-group">
                  <label for="title"></label>
                  <input type="text" id="title" class="form-control"
                    v-model="tempCoupon.title"
                    placeholder="">
                </div>
                <div class="form-group">
                  <label for="promo_code"></label>
                  <input type="text" id="promo_code" class="form-control"
                    v-model="tempCoupon.code"
                    placeholder="">
                </div>
                <div class="form-group">
                  <label for="due_date"></label>
                  <input type="date" id="due_date" class="form-control"
                    v-model="tempCoupon.due_date"
                    placeholder="">
                </div>
                <div class="form-group">
                  <label for="percent"></label>
                  <input type="number" id="percent" class="form-control"
                    v-model="tempCoupon.percent"
                    placeholder="ex: 10">
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input type="checkbox" class="form-check-input" id="is_enabled"
                      v-model="tempCoupon.is_enabled"
                      :true-value="1" :false-value="0">
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updateCoupon"></button>
          </div>
        </div>
      </div>
    </div>
    <!--  Modal -->
    <div class="modal fade" id="deleteCouponModal" tabindex="-1" role="dialog"
        aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id=""> Coupon</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             <strong class="text-danger">{{ tempCoupon.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="deleteCoupon"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';

export default {
  data() {
    return {
      coupons: [],
      tempCoupon: {},
      pagination: {},
      isNew: false,
      isLoading: false,
    };
  },
  methods: {
    getCoupons(page = 1) {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        vm.isLoading = false;
        vm.coupons = response.data.coupons;
        vm.pagination = response.data.pagination;
      });
    },
    editCouponModal(isNew, item) {
      if (isNew) {
        // 
        this.tempCoupon = {};
        this.isNew = true;
      } else {
        // 
        //   Object.assign
        this.tempCoupon = Object.assign({}, item);
        this.isNew = false;
      }
      $('#editCouponModal').modal('show');
    },
    deleteCouponModal(item) {
      this.tempCoupon = Object.assign({}, item);
      $('#deleteCouponModal').modal('show');
    },
    updateCoupon() {
      let url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
      let httpMethod = 'post';
      const vm = this;
      if (!vm.isNew) {
        url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
        httpMethod = 'put';
      }
      this.$http[httpMethod](url, { data: vm.tempCoupon }).then((response) => {
        if (response.data.success) {
          $('#editCouponModal').modal('hide');
          vm.getCoupons();
        } else {
          $('#editCouponModal').modal('hide');
          vm.getCoupons();
        }
      });
    },
    deleteCoupon() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
      vm.isLoading = true;
      this.$http.delete(url).then(() => {
        vm.isLoading = false;
        $('#deleteCouponModal').modal('hide');
        vm.getCoupons();
      });
    },
  },
  created() {
    this.getCoupons();
  },
};
</script>



